.button {
  @include button-reset;
  position: relative;
  max-width: 230px;
  min-width: 122px;
  margin-bottom: 25px;
  padding: 15px 64px 17px 30px;
  text-align: left;
  color: $text-white;
  background-color: $color-orange;
  border-radius: 25px;
  transition: $transition;

  &:last-child {
    margin-bottom: 0;
  }

  &:focus,
  &:hover {
    outline: none;
    opacity: 0.8;
  }

  &:active {
    outline: none;
    opacity: 0.6;
  }

  &__paw {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 50%;
    right: 7px;
    width: 36px;
    height: 36px;
    margin-top: -18px;
    background-color: $text-white;
    border-radius: 50%;

    use {
      fill: $color-orange;
    }
  }

  &--white {
    padding: 15px 64px 17px 30px;
    color: $text-black;
    background-color: $text-white;

    .button__paw {
      background-color: $color-orange;

      use {
        fill: $text-white;
      }
    }
  }
}

@include tablet {
  .button {
    padding-bottom: 17px;
  }
}

@include mobile {
  .button {
    width: 100%;
    max-width: 100%;
    text-align: center;
    padding-left: 39px;

    &--white {
      width: 206px;
      padding-left: 30px;
    }

    &--sm {
      width: 122px;
      min-width: 122px;
    }
  }
}
