.slider-controls {
  width: 45.8%;
  margin-left: auto;
  margin-right: 79px;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &__list {
    margin-right: -10px;
    display: flex;
  }

  &__button {
    @include button-reset;
    width: 36px;
    height: 36px;
    margin-right: 12px;
    background-color: #ffffff;
    box-shadow: 0 2px 12px rgba(173, 152, 143, 0.25);
    border-radius: 50%;
    transition: $transition;

    &:last-child {
      margin-right: 0;
    }

    &:focus,
    &:hover {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.6;
    }

    &--left {
      svg {
        transform: rotate(180deg);
      }
    }
  }
}

.dots {
  @include list-reset;
  display: flex;
  margin-right: -12px;
}

.dots .swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  margin-right: 12px;
  background-color: $color-gray;
  border-radius: 50%;
  opacity: 1;

  &-active {
    background-color: $color-yellow;
  }
}

@include tablet {
  .slider-controls {
    margin: 0 auto;
    justify-content: center;
    margin-bottom: 20px;

    &__list {
      display: none;
    }
  }
}

@include mobile {
  .slider-controls {
    width: 100%;
    padding-right: 0;
  }
}
