.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  padding-top: 146px;
  background-color: rgba(255, 254, 253, 0.8);
  overflow: auto;

  &--show {
    display: block;
    z-index: 11;
  }
}

@include tablet {
  .overlay {
    padding-top: 134px;
  }
}

@include mobile {
  .overlay {
    padding-top: 103px;
  }
}
