.rooms {
  background-image: url("../img/fish.svg");
  background-position: left 141px;
  background-repeat: no-repeat;
  background-size: 484px 151px;

  h2 {
    text-align: center;
  }

  h3 {
    margin: 0;
    margin-bottom: 21px;
    padding: 0;
    font-family: $font-rubik;
    font-size: 22px;
    line-height: 26px;
    font-style: normal;
    font-weight: 500;
  }

  img {
    background-color: $color-img;
  }

  p {
    margin: 0;
    padding: 0;
    font-size: 16px;
    line-height: 19px;
    font-style: normal;
    font-weight: normal;
  }

  &__slider-container {
    padding-left: 6%;
  }

  &__list {
    @include list-reset;
    display: flex;
    margin-bottom: 33px;
  }

  &__item {
    display: flex;
    align-items: center;
    width: 100%;
  }

  &__img-container {
    flex-shrink: 0;
    width: 54.5%;
    max-height: 404px;
    border-radius: 10px;
    object-fit: cover;
  }

  &__info {
    flex-shrink: 0;
    width: 45.4%;
    padding: 35px 110px 41px 100px;
    background: #ffffff;
    box-shadow: 0 0 25px rgba(208, 208, 208, 0.25);
    border-radius: 8px;
    transform: translateX(-70px);
  }

  &__info-list {
    @include list-reset;
    padding-bottom: 13px;
  }

  &__info-item {
    position: relative;
    margin-bottom: 14px;
    padding-left: 22px;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      width: 13px;
      height: 13px;
      margin-top: -6px;
      background-color: $color-yellow;
      border-radius: 50%;
    }
  }

  &--page {
    background-position: left 67px;
  }
}

@include tablet {
  .rooms {
    background-position: left 133px;
    background-size: 339px 105px;

    h3 {
      margin-bottom: 16px;
      font-size: 20px;
      line-height: 24px;
    }

    p {
      font-size: 15px;
      line-height: 18px;
    }

    &__slider-container {
      max-width: 688px;
      padding-left: 0;
    }

    &__list {
      margin-bottom: 40px;
    }

    &__img-container {
      width: 442px;
      max-height: 326px;

      img {
        height: 326px;
      }
    }

    &__info {
      width: 345px;
      padding: 35px 30px 41px 40px;
      transform: translateX(-109px);
    }

    &__info-list {
      padding-bottom: 17px;
    }

    &__info-item {
      margin-bottom: 10px;
      padding-left: 19px;

      &::before {
        width: 11px;
        height: 11px;
        margin-top: -5px;
      }
    }

    &--page {
      background-position: left 102px;
    }
  }
}

@include mobile {
  .rooms {
    background-position: left 90px;
    background-size: 150px 46px;

    h3 {
      margin-bottom: 15px;
    }

    &__slider-container {
      max-width: 450px;
      margin: 0 auto;
    }

    &__list {
      margin-bottom: 20px;
    }

    &__item {
      flex-direction: column;
    }

    &__img-container {
      width: 100%;
      height: 192px;

      img {
        height: 192px;
      }
    }

    &__info {
      width: 100%;
      padding: 20px 25px 25px 25px;
      transform: translate(0);

      p {
        line-height: 21px;
      }
    }

    &__info-list {
      padding-right: 15px;
      padding-bottom: 13px;
    }

    &__info-item {
      margin-bottom: 7px;

      &::before {
        top: 5px;
        margin-top: 0;
      }
    }

    .rooms__no-m {
      display: none;
    }

    &--page {
      .rooms__info-item {
        p {
          line-height: 21px;
        }
      }
    }
  }
}
