.banner {
  position: relative;
  z-index: 1;
  min-height: 379px;
  padding-top: 70px;
  padding-right: calc(275 / 1200 * 100%);
  padding-bottom: 75px;
  padding-left: calc(275 / 1200 * 100%);

  background-image: url("../img/banner@1x.jpg");
  background-repeat: no-repeat;
  background-position: right bottom;
  background-size: auto 379px;
  background-color: $color-yellow;
  border-radius: 10px;
  overflow: hidden;

  @include retina {
    background-image: url("../img/banner@2x.jpg");
  }

  h2 {
    margin-top: 0;
    margin-bottom: 5px;
    font-family: $font-rubik;
    font-weight: 700;
    font-size: 36px;
    line-height: 36px;

    b {
      font-weight: 700;
      font-size: 56px;
      line-height: 66px;
    }
  }

  p {
    max-width: 330px;
    margin-top: 0;
    margin-bottom: 30px;
  }

  @include tablet {
    min-height: 359px;
    padding-top: 64px;
    padding-right: 50px;
    padding-bottom: 69px;
    padding-left: 50px;
    background-image: url("../img/banner-tablet@1x.jpg");
    background-size: auto 359px;

    @include retina {
      background-image: url("../img/banner-tablet@2x.jpg");
    }

    h2 {
      margin-bottom: 12px;

      b {
        line-height: 56px;
      }
    }

    p {
      max-width: 250px;
      font-size: 15px;
      line-height: 21px;
    }
  }

  @include mobile {
    min-height: 496px;
    padding-top: 45px;
    padding-right: 25px;
    padding-bottom: 40px;
    padding-left: 25px;

    text-align: center;
    background-image: url("../img/banner-mobile@1x.jpg");
    background-size: auto 496px;

    @include retina {
      background-image: url("../img/banner-mobile@2x.jpg");
    }

    h2 {
      font-size: 24px;
      line-height: 28px;

      b {
        font-size: 37px;
        line-height: 37px;
      }
    }

    p {
      margin-right: auto;
      margin-left: auto;
      margin-bottom: 25px;
    }
  }
}

.banner__button.button {
  display: flex;
  width: 206px;
  align-items: center;
  margin-bottom: 0;
  padding-left: 7px;
  padding-top: 7px;
  padding-right: 7px;
  padding-bottom: 7px;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;

  .button__paw {
    position: static;
    margin-top: 0;
  }

  span:first-child {
    margin-right: auto;
    margin-left: auto;
    padding-right: 10px;
    padding-left: 15px;
  }

  @include mobile {
    margin-right: auto;
    margin-left: auto;
  }
}

.banner__bg-icon {
  position: absolute;
  top: -15px;
  left: -15px;
  z-index: -1;
  width: 241px;
  height: 206px;
  transform: rotate(135deg);

  use {
    fill: rgba(255, 254, 253, 0.2);
  }

  @include tablet {
    left: -25px;
    top: -10px;
    width: 153px;
    height: 131px;
  }

  @include mobile {
    left: -20px;
    top: -5px;
    width: 86.15px;
    height: 73.76px;
  }
}
