.main {
  h1,
  h2,
  b {
    margin: 0;
    padding: 0;
    font-family: $font-rubik;
    font-size: 62px;
    line-height: 73px;
    font-style: normal;
    font-weight: bold;
  }

  h1 {
    margin-bottom: 2px;
  }

  h2,
  b {
    margin-bottom: 61px;
    font-size: 36px;
    line-height: 43px;
    text-align: center;
  }

  b {
    display: block;
  }

  h3 {
    margin: 0;
    margin-bottom: 9px;
    padding: 0;
    font-family: $font-rubik;
    font-size: 22px;
    line-height: 26px;
    font-weight: 500;
  }

  p {
    margin: 0;
    padding: 0;
    font-family: $font-lato;
    font-size: 16px;
    line-height: 22px;
    font-style: normal;
    font-weight: normal;
  }

  &__intro {
    padding: 149px 198px 153px;
    background-image: url("../img/koteika-mask.svg"), url("../img/koteika@1x.jpg");
    background-size: cover;
    background-position: left center;
    background-repeat: no-repeat;
    background-color: $color-img;

    p {
      margin-bottom: 36px;
      font-size: 20px;
      line-height: 24px;
      font-weight: 500;
    }

    p:first-child {
      margin: 0;
      font-size: 14px;
      line-height: 17px;
    }
  }

  &__rooms {
    h2:first-of-type {
      padding-right: 0;
    }
  }

  &__reviews {
    h2:first-of-type {
      padding-right: 0;
    }
  }
}

.advantages {
  background-image: url("../img/paw.svg");
  background-position: 112% -28px;
  background-repeat: no-repeat;
  background-size: 458px 426px;
  overflow: hidden;

  &__list {
    @include list-reset;
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    margin-right: -2.5%;
    margin-bottom: -30px;

    .advantages__item {
      margin-right: 2.5%;
      margin-bottom: 19px;
    }
  }

  &__item {
    width: 30.8%;
    min-height: 321px;
    padding: 30px;
    padding-bottom: 25px;
    padding-right: 25px;
    background-color: #ffffff;
    box-shadow: 0 0 25px rgba(208, 208, 208, 0.25);
    border-radius: 8px;

    &--temp {
      p {
        padding-right: 20px;
      }
    }

    &--video {
      p {
        padding-right: 35px;
      }
    }

    &--taxi {
      p {
        padding-right: 55px;
      }
    }

    &--food {
      p {
        padding-right: 55px;
      }
    }
  }

  &__icon,
  img {
    margin-bottom: 25px;
    background-color: $color-img;
    border-radius: 50%;
  }
}

@include retina {
  .main__intro {
    background-image: url("../img/koteika-mask.svg"), url("../img/koteika@2x.jpg");
  }
}

@include tablet {
  .main {
    h2,
    b {
      margin-bottom: 51px;
    }

    h3 {
      margin-bottom: 8px;
      font-size: 20px;
      line-height: 28px;
    }

    p {
      font-size: 15px;
      line-height: 21px;
    }

    &__intro {
      min-height: 868px;
      padding: 0 20px;
      padding-top: 136px;
      text-align: center;
      background-image: url("../img/koteika-tablet@1x.jpg");
      background-size: cover;
      background-position: bottom;

      p {
        font-size: 20px;
        line-height: 24px;
      }
    }
  }

  .advantages {
    background-position: 132% -13px;
    background-size: 379px 344px;

    h3,
    p {
      padding-left: 15px;
    }

    &__list {
      justify-content: space-between;
      margin-right: 0;

      .advantages__item {
        margin-right: 0;
        margin-bottom: 30px;
        padding-left: 10px;
        padding-right: 10px;
      }
    }

    &__item {
      width: 48%;
      min-height: 285px;
      padding: 25px;
      padding-right: 5px;
      padding-bottom: 19px;

      &--temp {
        p {
          padding-right: 25px;
        }
      }

      &--food,
      &--taxi {
        p {
          padding-right: 50px;
        }
      }
    }

    &__icon,
    img {
      width: 75px;
      height: 75px;
      margin-bottom: 19px;
      margin-left: 15px;
    }
  }

  @include retina {
    .main__intro {
      background-image: url("../img/koteika-tablet@2x.jpg");
    }
  }
}

@include mobile {
  .main {
    h1 {
      font-size: 44px;
      line-height: 52px;
      margin-bottom: 7px;
    }

    h2,
    b {
      margin-bottom: 33px;
      font-size: 28px;
      line-height: 33px;
    }

    h3 {
      margin-bottom: 8px;
      font-size: 18px;
      line-height: 25px;
    }

    p {
      font-size: 15px;
      line-height: 21px;
    }

    &__intro {
      min-height: 457px;
      padding-top: 47px;
      background-image: url("../img/koteika-mobile@1x.jpg");
      background-size: cover;
      background-position: bottom;

      p {
        font-size: 16px;
        line-height: 19px;
        margin-bottom: 37px;
      }

      p:first-child {
        font-size: 12px;
        line-height: 14px;
      }
    }

    &__rooms {
      h2:first-of-type {
        padding-right: 0;
      }
    }
  }

  .advantages {
    background-position: 128% 36px;
    background-size: 149px 137px;

    &__list {
      justify-content: center;

      .advantages__item {
        padding-right: 10px;
        padding-bottom: 20px;
      }
    }

    &__item {
      width: 100%;
      min-height: 206px;

      &--video {
        margin-bottom: 31px;

        p {
          padding-right: 0;
        }
      }

      &--taxi {
        p {
          padding-right: 13px;
        }
      }

      &--food {
        p {
          padding-right: 10px;
        }
      }

      &--vet {
        p {
          padding-right: 35px;
        }
      }
    }

    &__icon,
    img {
      width: 60px;
      height: 60px;
      margin-bottom: 20px;
    }
  }

  @include retina {
    .main__intro {
      background-image: url("../img/koteika-mobile@2x.jpg");
    }
  }
}
