@mixin tablet {
  @media (max-width: $desktop-width - 1px) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: $tablet-width - 1px) {
    @content;
  }
}

@mixin desktop-only {
  @media (min-width: $desktop-width) {
    @content;
  }
}

@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

//   
@mixin title-reset {
  font-weight: normal;
  margin: 0;
  padding: 0;
}

@mixin field-reset {
  padding: 0;
  margin: 0;
  background-color: transparent;
  border: none;
  border-radius: 0;
  outline: none;
  appearance: none;
}

@mixin list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin link-reset {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

@mixin button-reset {
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  color: inherit;
}

@mixin transition($args...) {
  transition-property: $args;
  transition-duration: 0.2s;
}

// 
@mixin hover {
  transition: opacity $transition;

  &:hover,
  &:focus {
    outline: none;
    opacity: 0.8;
  }

  &:active {
    outline: none;
    opacity: 0.6;
  }
}
