.social {
  list-style: none;
}

.social__link {
  @include hover;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;

  @include tablet {
    padding: 9px;
  }

  @include mobile {
    &--wide {
      padding: 10px;
    }
  }
}

.social__icon {
  width: 22px;
  height: 22px;

  @include tablet {
    width: 24px;
    height: 24px;
  }
}
