.reviews {
  background-image: url("../img/paw-reverse.svg");
  background-position: 111% -47px;
  background-repeat: no-repeat;
  background-size: 417px 405px;
  overflow-x: hidden;

  &__list-container {
    width: 1470px;
  }

  &__list {
    @include list-reset;
    display: flex;
    top: 0;
    left: 0;
    margin-right: -30px;
    padding-bottom: 33px;
  }

  &__item {
    width: 470px;
    min-height: 206px;
    padding: 33px 35px 25px 70px;
    background: #ffffff;
    box-shadow: 0 0 25px rgba(208, 208, 208, 0.25);
    border-radius: 8px;

    & > p {
      position: relative;

      &::before {
        content: "";
        position: absolute;
        top: 4px;
        left: -36px;
        width: 29px;
        height: 23px;
        background-image: url("../img/quotes.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
    }
  }

  &__container {
    display: flex;
    justify-content: space-between;
    margin-top: 17px;

    p {
      font-size: 14px;
      line-height: 17px;
      color: #848484;
    }
  }
}

@include tablet {
  .reviews {
    background-position: 110% -16px;
    background-size: 293px 291px;

    &__list-container {
      width: 914px;
      margin-bottom: 7px;
    }

    &__list {
      padding-bottom: 28px;
    }

    &__item {
      padding: 31px 33px 14px 70px;
    }

    &__container {
      margin-top: 17px;
    }
  }
}

@include mobile {
  .reviews {
    background-position: 128% 3px;
    background-size: 152px 143px;

    &__list-container {
      width: 100%;
    }

    &__list {
      padding-bottom: 12px;
    }

    &__item {
      width: 100%;
      padding: 21px 21px 20px 56px;

      & > p {
        &::before {
          top: 4px;
          left: -34px;
          width: 25px;
          height: 17px;
        }
      }
    }

    &__container {
      display: none;
    }
  }
}
