.offer {
  width: 100%;
  position: relative;
  padding-bottom: 25px;
  background-color: $color-white;
  border-radius: 6px;
  box-shadow: 0 0 25px rgba(208, 208, 208, 0.25);
  overflow: hidden;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    border: 1px solid $color-gray;
    border-radius: 6px;
  }

  h3 {
    margin-top: 0;
    margin-bottom: 12px;
    margin-right: 20px;
    margin-left: 20px;
    font-family: $font-rubik;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;

    @include tablet {
      margin-right: 25px;
      margin-left: 25px;
    }
  }
}

.offer__image {
  position: relative;
  margin-top: 0;
  margin-bottom: 20px;


  @include tablet {
    width: 100%;

    & > div {
      position: relative;
      width: 100%;
      //         
      padding-bottom: calc(185 / 324 * 100%);
    }
  }

  @include mobile {
    & > div {
      //         
      padding-bottom: calc(185 / 290 * 100%);
    }
  }
}

.offer__image-content {
  img {
    display: block;
    width: 100%;
  }

  @include tablet {
    position: absolute;
    width: 100%;
    height: 100%;

    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
      object-position: center;
    }
  }
}

.offer__description {
  @include list-reset;
  margin-right: 10px;
  margin-bottom: 20px;
  margin-left: 20px;
  font-size: 14px;
  line-height: 17px;

  b {
    margin-left: 6px;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
  }

  @include tablet {
    margin-right: 25px;
    margin-left: 25px;
  }

  @include mobile {
    margin-right: 10px;
  }
}

.offer__description-item {
  position: relative;
  margin-bottom: 7px;

  &--flex {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 3px;
  }
}

.offer__hint {
  position: absolute;
  display: none;

  padding-top: 8px;
  padding-right: 10px;
  padding-bottom: 8px;
  padding-left: 10px;
  font-weight: 600;
  font-size: 12px;
  line-height: 14px;
  background-color: $text-white;
  border: 1px solid $color-yellow;
  border-radius: 4px;
  box-shadow: 0 2px 12px rgba(173, 152, 143, 0.18);

  @include desktop-only {
    left: 110px;
    top: -48px;

    &--right {
      left: auto;
      right: 5px;
      top: -48px;
    }

    &--two-line {
      left: -10px;
      right: auto;
      top: -25px;
    }
  }

  @include tablet {
    left: 110px;
    top: -48px;

    &--right {
      left: 110px;
    }

    &--narrow {
      left: 200px;
    }
  }

  @include mobile {
    left: 105px;
    top: -46px;

    &--right {
      left: 105px;
    }

    &--narrow {
      left: 190px;
    }
  }
}

.offer__icon {
  position: relative;
  vertical-align: middle;
  margin-top: 2px;
  margin-right: 7px;
  cursor: pointer;

  &:first-of-type {
    margin-left: 13px;
  }

  &::before {
    content: "";
    position: absolute;
    left: 50%;
    top: -21px;
    z-index: 1;
    display: none;
    width: 11px;
    height: 11px;
    background-color: $text-white;
    border-left: 1px solid $color-yellow;
    border-bottom: 1px solid $color-yellow;
    transform: rotate(-45deg);
    transform-origin: left bottom;
  }

  @include tablet {
    margin-right: 14px;
  }

  @include mobile {
    &:nth-last-child(2) {
      margin-right: 0;
    }

    &::before {
      top: -19px;
    }
  }
}

.offer__icon:hover + .offer__hint {
  display: block;
}

.offer__icon:hover::before {
  display: block;
}

.offer__button.button {
  display: flex;
  width: calc(100% - 20px - 20px);
  max-width: none;
  align-items: center;
  margin-right: 20px;
  margin-bottom: 0;
  margin-left: 20px;
  padding-left: 6px;
  padding-top: 6px;
  padding-right: 6px;
  padding-bottom: 6px;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;

  .button__paw {
    position: static;
    margin-top: 0;
    width: 34px;
    height: 34px;
  }

  span:first-child {
    margin-right: auto;
    margin-left: auto;
    padding-right: 10px;
    padding-left: 15px;
  }

  @include tablet {
    width: calc(100% - 25px - 25px);
    margin-right: 25px;
    margin-left: 25px;
  }
}
