.filter {
  font-family: $font-lato;
  font-size: 14px;
  line-height: 17px;
  font-style: normal;
  font-weight: normal;
  color: $text-black;

  form {
    position: relative;
  }

  fieldset {
    margin: 0;
    padding: 0;
    border: none;

    label:last-child {
      margin-bottom: 32px;
    }
  }

  h3 {
    margin: 0;
    margin-bottom: 16px;
    padding: 0;
    font-size: 16px;
    line-height: 19px;
    font-weight: 500;

    span {
      font-size: 14px;
      line-height: 17px;
      font-weight: normal;
    }
  }

  label {
    display: flex;
    margin-bottom: 15px;
    align-items: center;
    transition: $transition;

    &[for="from"],
    &[for="up-to"] {
      display: inline-block;
      position: relative;
      width: 100px;
      margin: 0;
      margin-bottom: 32px;
      font-size: 0;

      &:first-of-type {
        margin-right: 6px;
      }

      &::after {
        content: "";
        position: absolute;
        top: 4px;
        left: 5px;
        font-family: $font-lato;
        font-size: 14px;
        line-height: 17px;
        font-style: normal;
        font-weight: normal;
      }
    }

    &:hover,
    &:focus {
      cursor: pointer;
      outline: none;
      opacity: 0.8;
    }

    &:active {
      cursor: pointer;
      outline: none;
      opacity: 0.6;
    }

    &[for="up-to"] {
      &::after {
        content: "";
      }
    }

    span {
      flex-shrink: 0;
      position: relative;
      width: 20px;
      height: 20px;
      margin-right: 10px;
      border: 1px solid $color-gray;
    }
  }

  input[type="text"] {
    width: 100%;
    padding: 2px 5px 3px 24px;
    font-family: $font-lato;
    font-size: 14px;
    line-height: 17px;
    font-style: normal;
    font-weight: normal;
    color: $text-black;
    outline: none;
    border: 1px solid #e1e1e1;
    border-radius: 0;
    appearance: none;

    &::-webkit-input-placeholder {
      color: $text-black;
    }

    &::-moz-placeholder {
      color: $text-black;
    }

    &:-moz-placeholder {
      color: $text-black;
    }

    &:-ms-input-placeholder {
      color: $text-black;
    }

    &:hover,
    &:focus {
      outline: none;
    }

    &:active {
      outline: none;
    }
  }

  input[type="checkbox"] {
    display: none;

    &:checked + span::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      width: 10px;
      height: 9px;
      margin-top: -4px;
      margin-left: -5px;
      background-image: url("../img/tick.svg");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
    }
  }

  button {
    @include button-reset;
    width: 185px;
    margin-bottom: 15px;
    padding: 11px 10px 10px;
    font-family: $font-lato;
    font-size: 14px;
    line-height: 17px;
    font-style: normal;
    font-weight: 600;
    color: $text-black;
    border: 2px solid $color-yellow;
    border-radius: 25px;
    background-color: $color-yellow;
    transition: $transition;

    &:last-child {
      background-color: transparent;
      margin-bottom: 0;
    }

    span:last-child {
      display: none;
    }

    &:hover,
    &:focus {
      outline: none;
      opacity: 0.8;
    }

    &:active {
      outline: none;
      opacity: 0.6;
    }
  }
}

.filter .filter__btn-close {
  @include button-reset;
  display: none;
  position: absolute;
  top: 26px;
  right: 25px;
  width: 21px;
  height: 20px;
  margin: 0;
  background-color: transparent;
  border: none;

  &::after,
  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 21px;
    height: 2px;
    margin-left: -8px;
    margin-top: -1px;
    background-color: $text-black;
    transform: rotate(45deg);
  }

  &::before {
    transform: rotate(135deg);
  }
}

@include tablet {
  .filter {
    fieldset {
      label:last-child {
        margin-bottom: 27px;
      }

      &:last-of-type {
        label:last-child {
          margin-bottom: 34px;
        }
      }
    }

    label {
      span {
        width: 24px;
        height: 24px;
      }

      &[for="from"],
      &[for="up-to"] {
        width: 99px;
        margin-bottom: 29px;

        &::after {
          top: 9px;
          left: 8px;
        }

        &:first-of-type {
          margin-right: 9px;
        }
      }

      &[for="up-to"] input {
        padding-left: 30px;
      }
    }

    input[type="text"] {
      padding: 8px 8px 7px 26px;
    }

    button {
      padding: 10px 10px 11px;

      span:first-child {
        display: none;
      }

      span:last-child {
        display: inline;
      }
    }
  }

  .filter .filter__btn-close {
    display: block;
  }
}

@include mobile {
  .filter {
    label {
      &[for="from"],
      &[for="up-to"] {
        &::after {
          top: 10px;
        }
      }
    }

    button {
      width: 240px;
    }
  }

  .filter .filter__btn-close {
    top: 15px;
    right: 19px;
  }
}

button.filter__toggle {
  @include button-reset;
  display: none;
  min-width: 121px;
  width: auto;
  margin-bottom: 0;
  padding-top: 9px;
  padding-right: 20px;
  padding-bottom: 9px;
  padding-left: 20px;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  background-color: $color-white;
  border-radius: 25px;
  box-shadow: 0 2px 12px rgba(173, 152, 143, 0.18);

  span {
    @include hover;
  }

  @include tablet {
    display: flex;
  }

  @include mobile {
    min-width: 111px;
    padding-right: 15px;
    padding-left: 15px;
  }
}

.filter__toggle-icon {
  margin-right: 8px;

  use {
    fill: $text-black;

    @include transition(fill);
  }
}

.filter__toggle:hover use {
  fill: $color-orange;
}

.filter__overlay {
  @include tablet {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 2;
    display: none;
    padding-top: 136px;
    padding-left: 20px;
    padding-bottom: 45px;

    background: rgba(255, 254, 253, 0.8);
  }

  @include mobile {
    padding-top: 102px;
    padding-right: 15px;
    padding-left: 15px;
  }
}

.filter__inner {
  @include tablet {
    position: relative;
    width: 332px;
    margin: auto;
    padding-top: 50px;
    padding-right: 60px;
    padding-bottom: 50px;
    padding-left: 60px;

    background-color: $color-white;
    border-radius: 6px;
    box-shadow: 0 2px 12px rgba(173, 152, 143, 0.18);
  }

  @include mobile {
    width: 290px;
    padding-top: 45px;
    padding-right: 25px;
    padding-left: 25px;
  }
}

.filter--show {
  @include tablet {
    .filter__overlay {
      display: flex;
      overflow: auto;
    }
  }
}

.filter__hint {
  position: absolute;
  left: 0;
  top: 62px;
  display: none;
  width: 100%;

  padding-top: 8px;
  padding-right: 10px;
  padding-bottom: 8px;
  padding-left: 10px;
  font-weight: 600;
  font-size: 12px;
  line-height: 14px;
  background-color: $text-white;
  border: 1px solid $color-yellow;
  border-radius: 4px;
  box-shadow: 0 2px 12px rgba(173, 152, 143, 0.18);
  z-index: 10;

  @include tablet {
    left: 0;
    top: 72px;
  }
}

@media screen and (-webkit-min-device-pixel-ratio: 0) {
  select,
  textarea,
  input {
    font-size: 16px;
  }
}

@media screen and (-webkit-min-device-pixel-ratio: 0) {
  select:focus,
  textarea:focus,
  input:focus {
    font-size: 16px;
  }
}
