.sorting {
  position: relative;
  z-index: 1;
  min-width: 174px;
  min-height: 35px;

  @include mobile {
    min-width: 154px;
  }
}

.sorting__toggle {
  @include button-reset;
  position: absolute;
  right: 25px;
  top: 0;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 35px;
  height: 35px;

  @include mobile {
    right: 20px;
  }
}

.sorting__toggle-icon {
  @include transition(transform);

  use {
    fill: none;
    stroke: $text-black;

    @include transition(stroke);
  }
}

.sorting__toggle:hover use {
  stroke: $color-orange;
}

.sorting__list {
  @include list-reset;
  min-width: 174px;
  padding-right: 45px;
  padding-left: 20px;
  font-size: 14px;
  line-height: 17px;
  background-color: $color-white;
  border-radius: 25px;
  box-shadow: 0 2px 12px rgba(173, 152, 143, 0.18);

  @include mobile {
    min-width: 154px;
    padding-right: 40px;
    padding-left: 15px;
  }
}

.sorting__item {
  display: none;
  padding-top: 4px;
  padding-bottom: 4px;

  &--current {
    display: block;
  }
}

.sorting__link {
  @include link-reset;
  @include hover;
  display: block;
  padding-top: 5px;
  padding-bottom: 5px;
}

.sorting__direction--reverse {
  transform: rotate(180deg);
}

.sorting--show {
  .sorting__toggle-icon {
    transform: rotate(180deg);
  }

  .sorting__list {
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 4px;
  }

  .sorting__item {
    display: block;
  }
}
