.nav__list {
  @include list-reset;

  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: center;
  margin-left: -15px;
  margin-right: -15px;

  @include mobile {
    display: block;

    &--col {
      display: flex;
      justify-content: flex-start;
      margin-right: -10px;
      margin-left: -10px;

      .nav__item {
        width: calc(130 / 310 * 100%);
        padding-right: 10px;
        padding-left: 10px;
      }

      .nav__item:nth-child(even) {
        order: 1;
      }
    }
  }
}

.nav__item {
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 16px;
  padding-bottom: 16px;

  @include mobile {
    display: flex;
    padding-top: 8px;
    padding-bottom: 7px;
  }
}

.nav__link {
  @include link-reset;
  @include hover;

  display: block;

  span:last-child {
    display: none;
  }

  @include mobile {
    span:last-child {
      display: inline;
    }

    span:first-child {
      display: none;
    }
  }
}
