.header {
  padding-top: 12px;
  padding-bottom: 12px;

  @include mobile {
    position: relative;
  }
}

.header__wrapper {
  display: flex;

  @include mobile {
    flex-wrap: wrap;
  }
}

.header__logo {
  flex-shrink: 0;
  align-self: center;
}

.header__toggle {
  @include button-reset;
  @include hover;

  display: none;

  @include mobile {
    position: absolute;
    top: 11px;
    right: 6px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;

    span {
      position: relative;
      width: 24px;
      height: 2px;
      background-color: $text-black;
      border-radius: 1px;

      &::before,
      &::after {
        content: "";
        position: absolute;
        left: 0;
        width: 24px;
        height: 2px;
        background-color: $text-black;
        border-radius: 1px;
      }

      &::before {
        top: -7px;
      }

      &::after {
        bottom: -7px;
      }
    }
  }
}

.header__nav {
  margin-left: auto;
  padding-left: 30px;
  font-weight: 600;
  line-height: 19px;

  @include mobile {
    width: 100%;
    padding-top: 37px;
    padding-bottom: 25px;
    padding-left: 0;
    font-family: $font-rubik;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
  }
}

.header__tel {
  display: none;

  @include mobile {
    @include link-reset;

    margin-bottom: 10px;
    font-family: $font-rubik;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
  }
}

.header__social {
  display: none;
  margin: 0 0 0 -10px;
  padding: 0 0 10px;

  @include mobile {
    width: 100%;
  }
}

.header--opened {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 3;
  overflow: auto;
  background-color: $color-yellow;

  @include mobile {
    .header__toggle {
      right: 12px;

      span {
        background: transparent;

        &::before {
          top: 0;
          transform: rotate(45deg);
        }

        &::after {
          bottom: 0;
          transform: rotate(-45deg);
        }
      }
    }

    .header__nav {
      display: block;
    }

    .header__tel {
      display: block;
    }

    .header__social {
      display: flex;
    }
  }
}

.header--closed {
  @include mobile {
    .header__nav {
      display: none;
    }
  }
}

.header--nojs {
  position: relative;
  background-color: $color-yellow;

  @include mobile {
    .header__toggle {
      display: none;
    }

    .header__nav {
      display: block;
    }

    .header__tel {
      display: block;
    }

    .header__social {
      display: flex;
    }
  }
}
