.map {
  position: relative;
  display: flex;
  overflow: hidden;
  z-index: 1;
}

.map__image {
  position: relative;
  width: 100%;
}

.map__fallback {
  position: absolute;
  top: 0;
  left: 0;
  width: 798px;
  height: 580px;

  @include tablet {
    height: 522px;
  }

  @include mobile {
    height: 300px;

    img {
      display: block;
      height: 100%;
      width: auto;
    }
  }
}
