.modal {
  display: block;
  position: relative;
  font-family: $font-lato;
  font-size: 16px;
  line-height: 19px;
  font-style: normal;
  font-weight: normal;
  text-align: center;
  background-color: $text-white;
  box-shadow: 0 0 25px rgba(208, 208, 208, 0.25);
  border-radius: 10px;

  h2,
  p:first-child {
    margin: 0;
    margin-bottom: 43px;
    padding: 0;
    font-family: $font-rubik;
    font-weight: bold;
    font-size: 28px;
    line-height: 33px;
  }

  p {
    margin: 0;
    margin-bottom: 46px;
    padding: 0;

    &:first-child {
      margin-bottom: 16px;
    }
  }

  &--book {
    width: 570px;
    min-height: 649px;
    padding: 63px 70px;
    padding-bottom: 45px;
    background-image: url("../img/paw-modal.svg");
    background-position: -28px -17px;
    background-size: 169px 145px;
    background-repeat: no-repeat;

    form {
      & > label {
        position: absolute;
        width: 1px;
        height: 1px;
        margin: -1px;
        padding: 0;
        overflow: hidden;
        border: 0;
        white-space: nowrap;
        clip-path: inset(100%);
        clip: rect(0 0 0 0);
      }
    }

    input {
      width: 100%;
      margin-bottom: 20px;
      padding: 16px 28px 15px;
      font: inherit;
      background-color: transparent;
      border: 1px solid #d7d7d7;
      border-radius: 25px;
      appearance: none;

      &::-webkit-input-placeholder {
        color: $text-black;
      }

      &::-moz-placeholder {
        color: $text-black;
      }

      &:-moz-placeholder {
        color: $text-black;
      }

      &:-ms-input-placeholder {
        color: $text-black;
      }

      &:focus,
      &:hover {
        outline: none;
        opacity: 0.8;
      }

      &:active {
        outline: none;
        opacity: 0.6;
      }
    }

    label span {
      margin-right: 5px;
    }

    div {
      display: flex;
      align-items: center;
      margin-bottom: 50px;

      & > span {
        margin-right: 20px;
      }

      label {
        &:first-of-type {
          margin-right: 17px;
        }
      }

      input {
        width: 129px;
        margin-bottom: 0;
        padding: 16px 15px 16px 20px;

        &::-webkit-input-placeholder {
          color: #b5b5b5;
        }

        &::-moz-placeholder {
          color: #b5b5b5;
        }

        &:-moz-placeholder {
          color: #b5b5b5;
        }

        &:-ms-input-placeholder {
          color: #b5b5b5;
        }
      }
    }

    button {
      @include button-reset;
      position: relative;
      max-width: 230px;
      padding: 14px 66px 16px 32px;
      font: inherit;
      color: $text-white;
      background-color: $color-orange;
      border-radius: 25px;
      transition: $transition;

      &:focus,
      &:hover {
        outline: none;
        opacity: 0.8;
      }

      &:active {
        outline: none;
        opacity: 0.6;
      }

      span:last-child {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 50%;
        right: 7px;
        width: 36px;
        height: 36px;
        margin-top: -18px;
        background-color: $text-white;
        border-radius: 50%;

        use {
          fill: $color-orange;
        }
      }
    }
  }

  &--accept {
    width: 570px;
    min-height: 269px;
    padding: 62px;
    padding-bottom: 43px;
    background-image: url("../img/paw-modal-reverse.svg");
    background-position: -28px 114%;
    background-size: 169px 145px;
    background-repeat: no-repeat;
  }

  &--hide {
    display: none;
  }

  &--show {
    margin: 0 auto;
    z-index: 12;
  }
}

button.modal__close {
  @include button-reset;
  position: absolute;
  top: 39px;
  right: 32px;
  width: 40px;
  height: 20px;
  margin: 0;
  background-color: transparent;
  border: none;

  &::after,
  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 23px;
    height: 2px;
    margin-left: -8px;
    margin-top: -1px;
    background-color: $text-black;
    transform: rotate(45deg);
  }

  &::before {
    transform: rotate(135deg);
  }

  &:focus,
  &:hover {
    outline: none;
    opacity: 0.8;
  }

  &:active {
    outline: none;
    opacity: 0.6;
  }
}

@include tablet {
  .modal {
    width: 502px;

    p {
      margin-bottom: 47px;

      &:first-child {
        margin-bottom: 14px;
      }
    }

    &--book {
      min-height: 629px;
      padding: 54px 29px 37px 31px;

      input {
        padding: 15px 29px;
        padding-bottom: 16px;
        margin-bottom: 20px;
      }

      div {
        & > span {
          margin-left: 6px;
        }

        input {
          width: 133px;
        }
      }
    }

    &--accept {
      min-height: 269px;
      padding-top: 63px;
    }
  }

  button.modal__close {
    top: 23px;
    right: 14px;
  }
}

@include mobile {
  .modal {
    width: 290px;
    font-size: 15px;
    line-height: 18px;

    h2,
    p:first-child {
      font-size: 24px;
      line-height: 28px;
      margin-bottom: 34px;
    }

    p {
      width: 155px;
      margin: 0 auto;
      margin-bottom: 30px;
    }

    p:first-child {
      width: 100%;
      margin-bottom: 9px;
    }

    label span {
      margin-right: 2px;
    }

    &--book {
      min-height: 656px;
      padding: 55px 15px;
      background-position: -16px -19px;
      background-size: 104px 94px;

      input {
        margin-bottom: 20px;
        padding: 16px 19px;

        &[type="email"] {
          margin-bottom: 16px;
        }
      }

      div {
        flex-wrap: wrap;
        margin-bottom: 34px;

        span:first-child {
          width: 100%;
          margin-bottom: 12px;
          text-align: left;
        }

        label {
          &:first-of-type {
            margin-right: 10px;
          }
        }

        input {
          width: 107px;
          padding: 16px 15px;
          padding-right: 10px;
        }
      }

      button {
        max-width: 100%;
        width: 100%;
        padding-bottom: 18px;
        font-size: 16px;
        line-height: 19px;
      }
    }

    &--accept {
      min-height: 277px;
      padding: 55px 15px;
      font-size: 16px;
      line-height: 22px;
      background-position: -11px 106%;
      background-size: 104px 94px;
    }
  }

  button.modal__close {
    top: 17px;
    right: 10px;
    width: 40px;
  }
}
