*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-width: $mobile-width;
  min-height: 100vh;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.4em;
  font-family: $font-lato;
  color: $text-black;
  background-color: $text-white;
}

main {
  flex: 1 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
}

.noscroll {
  overflow: hidden;
  max-height: 100vh;
}

.blur {
  filter: blur($blur);
}

.blur-catalog {
  .header,
  .catalog__title,
  .catalog__sorting,
  .catalog__result,
  .catalog__banner,
  .footer {
    filter: blur($blur);
  }
}
