.contacts {
  display: flex;
  flex-wrap: wrap;
  background-color: $color-yellow;

  h2 {
    margin-top: 0;
    margin-bottom: 40px;

    font-family: $font-rubik;
    font-weight: 700;
    font-size: 36px;
    line-height: 43px;

    @include tablet {
      margin-bottom: 30px;
    }

    @include mobile {
      font-size: 28px;
      line-height: 33px;
    }
  }
}

.contacts__info {
  flex-grow: 1;
  width: 385px;
  padding-top: 60px;
  padding-bottom: 17px;

  @include tablet {
    width: 369px;
    padding-top: 50px;
    padding-bottom: 15px;
  }

  @include mobile {
    width: 100%;
    padding-top: 35px;
    padding-bottom: 0;
  }
}

.contacts__wrapper {
  max-width: 370px;
  margin-left: auto;
  padding-right: 15px;

  @include tablet {
    width: 100%;
    margin-left: 0;
    padding-left: 45px;
  }

  @include mobile {
    padding-left: 15px;
  }
}

.contacts__list {
  font-style: normal;
  font-size: 16px;
  line-height: 19px;

  @include tablet {
    font-size: 15px;
    line-height: 18px;
  }
}

.contacts__item {
  padding-bottom: 28px;

  b {
    display: block;
    padding-bottom: 8px;
    font-weight: 700;
  }

  @include tablet {
    padding-bottom: 23px;
  }
}

.contacts__link {
  @include link-reset;
  @include hover;
}

.contacts__map {
  flex-grow: 1;
  width: 615px;
  min-height: 580px;

  @include tablet {
    width: 399px;
    min-height: 522px;
  }

  @include mobile {
    width: 100%;
    min-height: 300px;
  }
}
