.center {
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 98px;

  &--flex {
    display: flex;
    align-items: flex-start;
  }

  &--padding-t {
    padding-top: 130px;
  }

  &--padding-b {
    padding-bottom: 20px;
  }
}

.main__rooms .center--padding-t {
  padding-top: 123px;
}

.main__reviews .center--padding-t {
  padding-top: 121px;
}

.room-page .center--padding-t {
  padding-top: 70px;
}

.room-page .center--padding-b {
  padding-bottom: 61px;
}

.main__reviews .center--padding-b,
.rooms--page .center--padding-b {
  padding-bottom: 130px;
}

@include tablet {
  .center {
    padding: 0 45px;

    &--sm {
      // padding-right: 35px;
    }

    &--padding-t {
      padding-top: 110px;
    }

    &--padding-b {
      padding-bottom: 20px;
    }
  }

  .main__rooms .center--padding-t {
    padding-top: 92px;
  }

  .main__reviews .center--padding-t {
    padding-top: 81px;
  }

  .main__reviews .center--padding-b {
    padding-bottom: 90px;
  }

  .room-page .center--padding-t {
    padding-top: 60px;
  }

  .room-page .center--padding-b {
    padding-bottom: 50px;
  }

  .rooms--page .center--padding-b {
    padding-bottom: 89px;
  }
}

@include mobile {
  .center {
    min-width: 320px;
    padding: 0 15px;

    &--flex {
      flex-direction: column;
      max-width: 450px;
      margin: 0 auto;
    }

    &--padding-t {
      padding-top: 64px;
    }

    &--padding-b {
      padding-bottom: 10px;
    }
  }

  .main__rooms .center--padding-t {
    padding-top: 52px;
  }

  .main__reviews .center--padding-t {
    padding-top: 35px;
  }

  .room-page .center--padding-t {
    padding-top: 40px;
  }

  .room-page .center--padding-b {
    padding-bottom: 13px;
  }

  .main__reviews .center--padding-b,
  .rooms--page .center--padding-b {
    padding-bottom: 40px;
  }
}
