.catalog {
  padding-bottom: 140px;

  h1 {
    width: 75%;
    margin-top: 0;
    margin-bottom: 48px;
    padding-left: 15px;
    padding-right: 15px;
    font-family: $font-rubik;
    font-weight: 700;
    font-size: 36px;
    line-height: 43px;
  }

  @include tablet {
    padding-bottom: 120px;

    h1 {
      order: -1;
      width: auto;
      margin-bottom: 42px;
      font-size: 36px;
      line-height: 43px;
    }
  }

  @include mobile {
    padding-bottom: 70px;

    h1 {
      width: 100%;
      margin-bottom: 32px;
      font-size: 28px;
      line-height: 33px;
    }
  }
}

.catalog__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  padding-top: 60px;

  @include tablet {
    padding-top: 50px;
  }

  @include mobile {
    padding-top: 35px;
  }
}

.catalog__sorting {
  align-self: flex-end;
  margin-left: auto;
  margin-bottom: 48px;
  padding-left: 15px;
  padding-right: 15px;

  @include tablet {
    margin-left: 0;
    margin-bottom: 42px;
  }

  @include mobile {
    align-self: center;
    margin-bottom: 30px;
    margin-left: auto;
  }
}

.catalog__filters {
  width: 25%;
  padding-right: 15px;
  padding-left: 15px;

  @include tablet {
    align-self: flex-end;
    position: relative;
    order: -1;
    width: auto;
    margin-left: auto;
    margin-bottom: 42px;
  }

  @include mobile {
    margin-left: 0;
    margin-bottom: 30px;
    padding-right: 10px;
  }
}

.catalog__result {
  width: 75%;
  margin-bottom: 110px;
  padding-top: 5px;
  padding-left: 15px;
  padding-right: 15px;

  @include tablet {
    width: 100%;
    margin-bottom: 90px;
    padding-top: 0;
    padding-left: 15px;
  }

  @include mobile {
    margin-bottom: 40px;
  }
}

.catalog__list {
  @include list-reset;

  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

.catalog__item {
  display: flex;
  width: calc(100% / 3);
  padding-right: 15px;
  padding-bottom: 30px;
  padding-left: 15px;

  @include tablet {
    width: 50%;
  }

  @include mobile {
    width: 100%;
  }
}

.catalog__banner {
  width: 100%;
}
