.room-page {
  p {
    margin: 0;
    margin-bottom: 18px;
    padding: 0;
    font-family: $font-lato;
    font-size: 16px;
    line-height: 19px;
    font-style: normal;
    font-weight: normal;
  }

  section {
    &:first-child {
      background-image: url("../img/paw-room.svg");
      background-position: 111% -3px;
      background-repeat: no-repeat;
      background-size: 326px 330px;
    }

    &:last-child {
      p {
        margin-bottom: 0;
      }
    }
  }

  h1,
  h2 {
    margin: 0;
    margin-bottom: 61px;
    padding: 0;
    font-family: $font-rubik;
    font-size: 36px;
    line-height: 43px;
    font-style: normal;
    font-weight: bold;
  }

  h1 {
    margin-top: -10px;
    margin-bottom: 27px;
  }

  &__column {
    width: 48%;

    & > p:last-of-type {
      margin-bottom: 26px;
    }

    & > .room-page__img-wrapper {
      width: 84%;
      max-height: 396px;
    }

    &:first-child {
      display: flex;
      width: 51.3%;
      max-height: 396px;
    }

    &:last-child {
      padding-left: 6%;
      padding-right: 20px;
    }
  }

  &__img-container {
    width: 12%;
    margin-right: 5%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;

    .room-page__img-wrapper {
      position: relative;
      width: 100%;
      height: 28%;
      margin-bottom: 35%;
      border-radius: 4px;
      overflow: hidden;
      opacity: 0.5;

      img {
        position: relative;
        bottom: 0;
        border-radius: 4px;
      }

      &:last-child {
        margin-bottom: 0;
      }

      &--top {
        img {
          bottom: -14px;
          right: 5px;
          transform: scale(1.3);
        }
      }

      &--mid {
        img {
          bottom: 34%;
          right: 0;
          transform: scale(1.5);
        }
      }
    }
  }

  &__list {
    @include list-reset;
    margin-bottom: 23px;
    margin-top: -3px;
  }

  &__item {
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
    margin-bottom: 10px;

    p {
      margin-bottom: 0;
    }

    span {
      margin-right: 8px;
    }

    &--bed {
      span {
        width: 16px;
        height: 16px;
        background-image: url("../img/icon-bed.svg");
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
      }
    }

    &--house {
      span {
        width: 16px;
        height: 16px;
        background-image: url("../img/icon-house.svg");
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
      }
    }

    &--play {
      span {
        width: 16px;
        height: 16px;
        background-image: url("../img/icon-toy.svg");
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
      }
    }

    &--claw {
      span {
        width: 16px;
        height: 16px;
        background-image: url("../img/icon-claw.svg");
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
      }
    }
  }

  &__price {
    margin-left: 10px;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
  }
}

@include tablet {
  .room-page {
    p {
      margin-bottom: 15px;
      font-size: 15px;
      line-height: 18px;
    }

    h1 {
      margin-bottom: 25px;
    }

    h2 {
      margin-bottom: 52px;
      padding-right: 50px;
    }

    &__column {
      & > .room-page__img-wrapper {
        width: 100%;
        min-height: 288px;
        margin-bottom: 22px;
        border-radius: 8px;

        img {
          height: 100%;
        }
      }

      &:first-child {
        flex-wrap: wrap;
        width: 52.4%;
        max-height: 100%;
      }

      &:last-child {
        padding-left: 8.7%;
        padding-right: 10px;
      }
    }

    &__img-container {
      width: 100%;
      min-height: 78px;
      margin-right: 0;
      order: 1;
      flex-direction: row;

      .room-page__img-wrapper {
        width: 29%;
        height: 100%;
        margin-bottom: 0;
        flex-grow: 0;

        img {
          position: absolute;
        }

        &--top {
          img {
            top: -12%;
            right: 0;
            transform: scale(1);
          }
        }

        &--mid {
          img {
            top: -88%;
            right: 0;
            transform: scale(1);
          }
        }

        &--bot {
          img {
            top: -50%;
          }
        }
      }
    }

    &__list {
      margin-top: 0;
      margin-bottom: 17px;
    }

    &__item {
      margin-bottom: 13px;

      p {
        margin-bottom: 0;
      }

      span {
        width: 18px;
        height: 18px;
        margin-right: 10px;
      }

      &--house,
      &--play {
        margin-bottom: 15px;
      }
    }

    &__price {
      margin-left: 7px;
    }
  }
}

@include mobile {
  .room-page {
    h1,
    h2 {
      font-size: 28px;
      line-height: 33px;
    }

    h1 {
      margin-bottom: 19px;
    }

    h2 {
      padding: 0 40px;
      margin-bottom: 32px;
    }

    p {
      margin-bottom: 13px;
    }

    &__column {
      width: 100%;

      & > p:last-of-type {
        margin-bottom: 25px;
      }

      & > .room-page__img-wrapper {
        margin-bottom: 16px;

        img {
          width: 100%;
          border-radius: 8px;
        }
      }

      &:first-child {
        width: 100%;
        margin-bottom: 45px;
      }

      &:last-child {
        padding-left: 0;
        padding-right: 0;
      }
    }

    &__item {
      p {
        margin-bottom: 0;
      }
    }

    &__img-container {
      min-height: 64px;
    }
  }
}
