.footer__social {
  display: flex;
  margin: -6px 0 0 -10px;
  padding: 0 0 10px;

  @include mobile {
    margin-top: -6px;
    padding-bottom: 8px;
  }
}

.footer__navigation {
  padding-top: 12px;
  padding-bottom: 12px;

  @include mobile {
    padding-top: 30px;
  }
}

.footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 1000px;
  margin: 0 auto;
  padding-right: 15px;
  padding-left: 15px;

  @include tablet {
    padding-right: 45px;
    padding-left: 45px;
  }

  @include mobile {
    padding-right: 15px;
    padding-left: 15px;
  }
}

.footer__nav {
  font-weight: 600;
  line-height: 19px;

  @include mobile {
    width: 100%;
    padding-top: 12px;
    padding-bottom: 5px;
  }
}

.footer__bottom {
  padding-top: 18px;
  padding-bottom: 18px;
  font-size: 12px;
  line-height: 14px;
  color: $color-gray-dark;
  border-top: 1px solid $color-gray-lighten;

  a {
    @include link-reset;
    @include hover;
  }

  @include mobile {
    padding-top: 13px;

    a {
      width: 100%;
      padding-bottom: 15px;
    }
  }
}
